import React from 'react';
import LazyLoad from "react-lazyload";
import './Card.css';

// Diamonds
import dia_legendary from './imgs/diamonds/legendary.svg';
import dia_ultra from './imgs/diamonds/ultra.svg';
import dia_rare from './imgs/diamonds/rare.svg';
import dia_uncommon from './imgs/diamonds/uncommon.svg';
import dia_common from './imgs/diamonds/common.svg';


class Card extends React.Component {

  static rarity_names = ['common', 'uncommon', 'rare', 'ultra rare', 'legendary'];
  static rarity_images = [dia_common, dia_uncommon, dia_rare, dia_ultra, dia_legendary];

  render() {
    return (
      <LazyLoad className="item" height={120} offset={500}>
          <img src={process.env.PUBLIC_URL + `/assets/dogs/cyberdoggo_${String(this.props.item.id).padStart(4, '0')}_large.png`} alt="Dog" height="96px"/>
          <div className="item-desc">
            <div style={{flexGrow: "1"}} />
            <div className="rarity-label">
              <span className="item-name">{this.props.item.name}</span>
              <div style={{flexGrow: "1"}} />
              {this.props.item.sold &&
              <span className="sold">SOLD</span>
              }
            </div>
            <div className="rarity-label">
              <img className="rarity-diamond" src={Card.rarity_images[this.props.item.rarity]} alt="Rarity" height="15px"/>
              <span className={`rarity-${this.props.item.rarity}`}>{Card.rarity_names[this.props.item.rarity]}</span>
            </div>
            <span className="rarity-score">(Score: {this.props.item.score})</span>
            <div style={{flexGrow: "1"}} />
          </div>
      </LazyLoad>
    )
  }
}

export default Card
